<template>
  <main>

    <!--  -->
    <Menu       v-if="LOGIN"/>
    <Settings   v-if="LOGIN"/>

    <!--  -->
    <Nav        v-if="LOGIN"/>
    <SwitchNav  v-if="LOGIN"/>

    <!--   -->
    <Loading    v-show="LOADING"/>

    <!--   -->
    <Error      v-show="ERROR"/>
    <Warning    v-show="WARNING"/>

    <BackDrop   v-show="NAV & APP_MOBILE"/>

    <router-view/>
  </main>
</template>

<script>
import Menu from '@/components/Settings/Menu.vue'
import Settings from '@/components/Settings/Settings.vue'

import Nav from '@/components/Nav/Nav.vue'
import SwitchNav from '@/components/Nav/SwitchNav.vue'

import BackDrop from '@/components/BackDrop.vue'
import Loading from '@/components/Loading.vue'
import Error from '@/components/Error.vue'
import Warning from '@/components/Warning.vue'

export default {
    components: {
        Menu,
        Settings,
        Nav,
        SwitchNav,
        Loading,
        Error,
        BackDrop,
        Warning
    },
    computed: {
        LOGIN() {
            return this.$store.state.user.LOGIN
        },
        LOADING() {
            return this.$store.state.app.LOADING
        },
        ERROR() {
            return this.$store.state.app.ERROR
        },
        MENU() {
            return this.$store.state.app.MENU
        },
        NAV() {
            return this.$store.state.app.NAV
        },
        APP_MOBILE() {
            return this.$store.state.app.APP_MOBILE
        },
        WARNING() {
            return this.$store.state.app.WARNING
        },
    }
}
</script>